body{
  background: #e8eaf1;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
}

textarea{
  background: #eff1f5;
  outline: none; 
   border: none;
  border-radius: 5px;
  font-size: 20px;
  padding: 25px;
  
}

.preview{
  padding: 25px;
}